.main {
  //background: #ececec;
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 20px 15px 20px 15px;
  box-sizing: border-box;
  @include sumaho() {
    width: 100%;
    padding: 40px 0 20px 0;
    flex-direction: column;
  }
  //左
  &-left {
    width: 20%;
    @include sumaho() {
      width: 100%;
    }

    &-field {
      width: 100%;
      text-align: center;
    }

    &-cover {
      width: 220px;
      height: 220px;
      margin: 0 auto;
      background: white;

      & img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }

      @include sumaho() {
        width: 100px;
        height: 100px;
        object-fit: contain;
      }
    }

    &-backcolor {
      background: white;
    }

    &-name {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 10px;
    }

    &-info {
      display: flex;
      flex-direction: column;
      font-size: 16px;
      padding: 0 10px;
      box-sizing: border-box;
    }

    &-link {
      color: #2a2a2a;
      display: block;
      margin-bottom: 10px;
      border: #2a2a2a solid 1px;
      border-radius: 3px;

      &:hover {
        background: #2a2a2a;
        color: white;
      }
    }
  }

  //真ん中
  &-center {
    width: 60%;
    padding: 0 15px;
    box-sizing: border-box;
    @include sumaho() {
      width: 100%;
    }

    &-title {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    &-postarea {
      display: flex;
      flex-wrap: wrap;
      margin-right: -5px;
    }

    &-tagarea {
      & a {
        color: #2a4da5;
        font-size: 14px;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  //右
  &-right {
    width: 20%;
    padding: 0 15px;
    box-sizing: border-box;
    @include sumaho() {
      width: 100%;
    }

    &-inner {

    }
  }
}

.post-card {
  position: relative;
  width: calc(33.3% - 5px);
  margin-right: 5px;
  margin-bottom: 10px;
  @include sumaho() {
    width: calc(50% - 5px);
  }

  &:last-child {
  }

  &-cover {
    background: white;
    width: 100%;
    height: 220px;

    & img {
      object-fit: contain;
      width: 100%;
      height: 100%;

    }
  }

  &-icon {
    width: 35px;
    height: 35px;
    object-fit: cover;
    margin-right: 5px;

    & img {
      border-radius: 50%;
      height: 100%;
    }
  }

  &-info {
    font-size: 16px;
    color: #2a2a2a;

    &-inner {
      display: flex;
      padding: 5px;

      &-text {
        display: flex;
        flex-direction: column;
      }
    }
  }

  &-title {
    font-weight: bold;

    & a {
      color: #2a2a2a;
    }
  }

  &-name {
    font-size: 14px;

    & a {
      color: #585858;
    }
  }

  &-count {
    font-size: 14px;
    color: #585858;
  }
}

.rank-card {
  margin-bottom: 10px;

  &-cover {
    width: 100%;
    height: 120px;
    background: white;

    & img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &-title {
    font-size: 16px;
    font-weight: bold;

    & a {
      color: #2a2a2a;
    }
  }

  &-user {
    font-size: 14px;
    color: #585858;
  }
}


.passchange_message {
  background: rgba(90, 188, 253, .5);
  width: 100%;
  padding: 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0%);
  display:none;
}
