.artwork-content {
  display: flex;
  max-width: 1500px;
  margin: 0 auto;
  padding: 20px 0 0 20px;
  box-sizing: border-box;
  @include sumaho() {
    width: 100%;
    flex-direction: column;
    padding: 60px 0 0 0;
  }
}
.artwork-content-left{
  width: 60%;
  padding: 0 15px;
  box-sizing: border-box;
  @include sumaho() {
    width: 100%;
  }
}
.artwork-img{
  width: 100%;
  & img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.artwork-info-main {
  display: flex;
  justify-content: space-between;
}

.artwork-title {
  font-size: 20px;
}

.star-icon {
  &:hover {
    cursor: pointer;
  }
}

.star-icon-active {
  color: #ffb93d;
  animation: star-anime .2s ease-out;
}

@keyframes star-anime {
  0% {
    transform: scale(0.2); }
  100% {
    transform: scale(1); }
}


.good-icon{
  &-active{
    animation: star-anime .2s ease-out;
  }
}

.artwork-info-content {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #8d9696;
}

.artwork-text-content {
  display: flex;
}

.artwork-evaluation {
  &::after {
    content: '・';
  }
}

.artwork-data {

}

.artwork-good {
  display: flex;
  align-items: center;
  &:hover {
    cursor: pointer;
  }
}
.artwork-tag{
  & a{
    color: #2a4da5;
  }
}

.comment-content{
margin-bottom: 20px;
}
.comment-title{
  font-size: 16px;
}
.form-comment{

}
.form-comment-input{
  width: 100%;
  box-sizing: border-box;
  margin-bottom:10px;
}
.comment-content-post{
  display: flex;
  margin-bottom: 15px;
}
.comment-icon{
  margin-right: 5px;
  flex-shrink: 0;
  & img{
    border-radius: 50%;
    width: 40px;
    height: 40px;
  }
}
.comment-post-area{
  font-size: 13px;
}
.comment-name{
  font-weight: bold;
}



.artwork-content-right{
  width: 40%;
  padding: 0 15px;
  box-sizing: border-box;
  @include sumaho() {
    width: 100%;
  }
}

.recommended-post{
  display: flex;
  margin-bottom:20px;
  @include newipad(){
    flex-direction: column;
  }
}
.recommended-img{
  max-width: 180px;
  height: 100px;
  margin-right: 10px;
  background: #ffffff;
  @include newipad(){
    max-width: 100%;
  }
  & img{
    object-fit: contain;
    width: 100%;
    height: 100%;
  }
}
.recommended-textarea{
  font-size: 14px;
}
.recommended-title{
  font-weight: bold;
}
.recommended-title{
  color: #2a2a2a;
}
.recommended-name{
  color: #8d9696;
}
.recommended-info{
  color: #8d9696;
}
