.art-post-form{
  display: flex;
  flex-direction: column;
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
}
.art-post-ul{
  display: flex;
  margin-bottom: 20px;
  font-size: 16px;
}
.art-post-img-warp{
  margin: 0 auto 20px auto;
  position: relative;
  font-size: 13px;
  text-align: center;
}
.art-post-file{
  position: absolute;
  width: 100%;
  height: 55px;
  top: 0;
  left: 0;
  opacity: 0;
  &:hover{
    cursor:pointer;
  }
}
.art-post-file-btn{
  max-width: 170px;
  margin: 0 auto;
  text-align: center;
  border-radius: 3px;
  padding: 15px;
  background: #444444;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  transition: .5s;
  &:hover{
    opacity: .7;
    cursor:pointer;
  }
}

.art-post-image-preview{
  max-width: 400px;
  height: 400px;
  margin: 0 auto;
  background: white;
  & img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.img_del_btn{
  color: #2a4da5;
  font-size: 13px;
}

.art-post-list-text{
  width: 30%;
  text-align: center;
}
.art-post-list-input{
  width: 70%;
}
.art-post-input{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  &-textarea{
    height: 180px;
  }
}

.u-art-post-text{
  font-size: 13px;
  text-align: center;
}

.art-post-btn{
  max-width: 200px;
  width: 100%;
  margin: 0 auto;
  background: #444444;
  color: #ffffff;
  transition: .5s;
  border-radius: 3px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  padding: 15px;
  &:hover{
    opacity: .7;
    cursor:pointer;
  }
}
