.user-info{
  margin: 0 auto 50px auto;
  display: flex;
  justify-content: center;
}
.user-text-wrap{
  //padding: 15px;
  //box-sizing: border-box;
  //background: white;
}
.user-page-icon{
  width: 100px;
  height: 100px;
  margin-right: 10px;
  & img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.user-page-name{
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}
.user-self-text{
  font-size: 16px;
}
.user-top-pagination{
  margin-bottom: 20px;
}

.c-top-warp{
  text-align: center;
  margin-bottom: 20px;
}

.c-top-warp-title{
  font-size: 16px;
  font-weight: bold;
}

.del-icon{
  transition: .5s;
  width: max-content;
  color: #2a2a2a;
  &:hover{
    opacity: .5;
    cursor: pointer;
  }
}



.model-wrap{
  font-size: 16px;
  display: flex;
  justify-content: space-around;
}
.model-text{
  font-size: 16px;
  margin-bottom: 10px;
  text-align: center;

}

.model-btn-del{
  padding: 5px 10px;
  border-radius: 3px;
  background: #c35757;
  color: #ffffff;
}

.model-btn-back{
  padding: 5px 10px;
  border-radius: 3px;
  background: #57a1c3;
  color: #ffffff;
}
