.form-profile {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  font-size: 16px;
  box-sizing: border-box;
  padding: 0 15px 0 15px;

  @include sumaho(){
  }
}

.form-profile-label {
  display: flex;
  flex-direction: column;

  &-upload {
    background: #d6d6d6;
    width: 220px;
    height: 220px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;

  }
}

.form-profile-input {
  width: 100%;
  padding: .25em;
  margin-bottom: 10px;
  box-sizing: border-box;

}

.form-profile-img-upload {
  //display: none;
  opacity: 0;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 4;
}
.preview-back-cover{
  background: #373737;
  opacity: .6;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
}
.preview-back-cover-icon{
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin:auto;
  z-index: 5;
  font-size: 20px;
  color: white;
}
.form-profile-preview-cover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;

}

.form-profile-image-box {
  width: 220px;
  height: 220px;
}
