$breakpoints: (
        'sm': 'screen and (max-width: 414px)',
        'md': 'screen and (max-width: 769px)',
        'lg-ipad':'screen and (max-width: 810px)',
        'lg': 'screen and (max-width: 1000px)',
        'xl': 'screen and (max-width: 1200px)',
);
@mixin ipadpro($breakpoint:xl) {
  @media #{map_get($breakpoints,$breakpoint)}{
    @content
  }
}

@mixin newipad($breakpoint:lg-ipad) {
  @media #{map_get($breakpoints,$breakpoint)}{
    @content
  }
}
@mixin ipad($breakpoint:md) {
  @media #{map_get($breakpoints,$breakpoint)}{
    @content
  }
}
@mixin sumaho($breakpoint:sm) {
  @media #{map_get($breakpoints,$breakpoint)}{
    @content
  }
}
