.search-main{
  flex-direction: column;
}

.search-warp{
  max-width: 1400px;
  margin: 0 auto;
  @include sumaho() {
    padding: 0 10px 0 10px;
  }
}

.search-content{
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  @include sumaho() {
    margin-right: -5px;
  }
}

.post-card-search{
  position: relative;
  width: calc(25% - 15px);
  margin-right: 15px;
  margin-bottom: 10px;
  @include sumaho() {
    width: calc(50% - 5px);
    margin-right: 5px;
  }
}
.search-result-text{
  font-size: 16px;
  margin-bottom: 15px;
}

.pagination{
  display: flex;
  justify-content: center;
  & ul{
    display: flex;
    & li{
      font-size: 13px;
      margin-right: 10px;
      & a{
        color: #2a2a2a;
        font-weight: bold;
        padding: 10px;
      }
    }
  }
  &-active{
    background: #2a2a2a;
    color: white !important;
  }
}
