.header {
  background-color: #eeeeee;

  @include sumaho() {
    width: 100%;
    z-index: 2;
    position: fixed;

  }
  &-inner {
    max-width: 1100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto 0px auto;
    padding: 5px 15px 15px 15px;
    box-sizing: border-box;
    @include sumaho() {
      padding: 5px 15px 5px 15px;
    }
  }

  &-title {
    font-size: 30px;
    font-weight: bold;
    @include ipad() {
      font-size: 16px;
    }

    & a {
      color: #2a2a2a;
    }
  }

  &-form {
    display: flex;
    position: relative;
    max-width: 300px;
    width: 100%;
    @include sumaho() {
      display: none;
    }
  }

  &-input {
    width: 100%;
    height: 30px;
    padding: 5px;
    box-sizing: border-box;
  }

  &-submit {
    height: 30px;
    box-sizing: border-box;
    position: absolute;
    right: 0;

    &:hover {
      cursor: pointer;
    }
  }

  //メニュー
  &__nav {
    font-size: 16px;

    padding: 0 15px 0 15px;
    box-sizing: border-box;
    background: #696969;


    & ul {
      max-width: 1100px;
      margin: 0 auto;
      display: flex;
      @include sumaho() {
        //display: none;
        position: fixed;
        background: #797979;
        top: 0;
        right: -100%;
        width: 100vw;
        height: 100vh;
        margin: 0;
        flex-direction: column;
        transition: .5s;
      }

      & li {
        margin-right: 5px;
        font-weight: bold;
        @include sumaho() {
          &:first-child {
            padding-top: 30px;
          }
        }

        &:hover {
          background: #2a2a2a;
          transition: .5s;
          @include sumaho() {
            background:none;
          }
        }

        & a {
          color: #ffffff;
          display: block;
          padding: 5px 10px;
        }
      }
    }
  }
}

.ul-active {
  @include sumaho() {
    transform: translate(-100%);

  }
}

//ハンバーガーメニュー
@include sumaho() {
  .menu-trigger,
  .menu-trigger span {
    display: inline-block;
    transition: all .4s;
    box-sizing: border-box;
  }
  .menu-trigger {
    position: relative;
    width: 27px;
    height: 30px;
    top: 6px;
    z-index: 3;
  }
  .menu-trigger span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #373737;
    border-radius: 4px;
  }
  .menu-trigger span:nth-of-type(1) {
    top: 0;
  }
  .menu-trigger span:nth-of-type(2) {
    top: 8px;
  }
  .menu-trigger span:nth-of-type(3) {
    bottom: 10px;
  }
  .menu-trigger.active span:nth-of-type(1) {
    transform: translateY(6px) rotate(-45deg);
  }
  .menu-trigger.active span:nth-of-type(2) {
    opacity: 0;
  }
  .menu-trigger.active span:nth-of-type(3) {
    transform: translateY(-10px) rotate(45deg);
  }

}
